
.multiselect {
  input, select, button {
    display: block;
    width: 100%;
  }

  .selected {
    .none-selected {
      font-style: italic;
    }

    ul {
      list-style-type: none;
      padding: 0.1rem;
      // text-align: center;
      li {
        display: inline-block;
        margin: 0.3rem 0.3rem;
        background: #dde;
        padding: 0.4rem 0.8rem;
        font-weight: bold;
        border-radius: 3px;
        cursor: pointer;

        &:hover {
          background: #e7e7f3;
        }

      }
    }
  }

}

.countries {

  border-style: solid;
  margin: 1.5rem 0.2rem;
  padding: 0.6rem;
  border-width: 1px;
  border-radius: 2px;
  background-color: #fff7f7;

}

.axis-options {
  border-style: solid;
  margin: 1.5rem 0.2rem;
  padding: 0.6rem;
  border-width: 1px;
  border-radius: 2px;
  background-color: #f7f7ff;

  .base-projection, .axis-op-chain, .axis-scale {
    span {
      display: block;
      width: 100%;
      font-style: italic;
      font-size: 85%;
      margin: 0;
    }
    select {
        display: block;
        width: 100%;
    }
    margin-bottom: 1rem;
  }
}
